import React from 'react';
import imgBurger from '../../assests/images/burger-logo.png';
import classes from './Logo.css';

const Logo = (props) => {
  return (
    <div className= {classes.Logo}
    	style= {{height: props.height}}>
    	<img src= {imgBurger} alt= "myBurger"/>
    </div>
  )
}

export default Logo;