import React from 'react';
import classes from './Input.css';

const input = (props) => {
  let inputElement= null;
  const inputClass= [classes.InputElement];
  if(props.invalid && props.shouldValidate && props.touched){
  	inputClass.push(classes.Invalid);
  }
  switch(props.elementtype){
  	case('input'):
  		inputElement= <input className= {inputClass.join('')} 
  		{...props.elementconfig}
  		value= {props.value}
  		onChange= {props.changed}
  		 />;
  		break;
  	case('email'):
  		inputElement= <input className= {inputClass.join('')}
  		 {...props.elementconfig}
  		 value= {props.value}
  		 onChange= {props.changed}
  		  />;
  		break;
  	case('textarea'):
  		inputElement= <textarea className= {inputClass.join('')}
  		 {...props}
  		 value= {props.value}
  		 onChange= {props.changed}
  		  />
  		break;
  	case('select'):
  		inputElement= <select className= {inputClass.join('')}
  		 {...props}
  		 value= {props.value}
  		 >
  			{props.elementconfig.options.map(optn => (
  				<option key= {optn.value}
  					value= {optn.value}>{optn.displayValue}</option>
  			))}console.log(optn.value);
  		</select>
  		break;
  	default:
  		inputElement= <input className= {inputClass.join('')} {...props} value= {props.value} />
  }
  return (
    <div className= {classes.Input}>
    	<label className= {classes.Label}>{props.label}</label>
    	{inputElement}
    </div>
  )
}

export default input;