import * as actionTypes from './actionTypes';
import axios from '../../axios-orders';

export const addIngredient= (name)=>{
	return{
		type: actionTypes.ADD_INGREDIENT,
		ingredientName: name
	};
};
export const deleteIngredient= (name)=>{
	return{
		type: actionTypes.DELETE_INGREDIENT,
		ingredientName: name
	};
};
export const setIngredients= (ingredients) =>{
	return{
		type: actionTypes.SET_INGREDIENTS,
		ingredients: ingredients
	};
};
export const failedIngredients= () =>{
	return{
		type: actionTypes.FAILED_INGREDIENTS
	};
};
export const initIngredients= () =>{
	return dispatch =>{
			axios.get('https://react-burger-5a4c7.firebaseio.com/ingredients.json')
			.then(response =>{
				dispatch(setIngredients(response.data));
			})
			.catch(error =>{
				dispatch(failedIngredients())
			});
	}
}
//action creators for burgerbuilder