import React, {Component} from 'react';
import {Redirect} from 'react-router-dom';
import Input from '../../components/UI/Input/Input';
import Spinner from '../../components/UI/Spinner/Spinner';
import classes from './Auth.css';
import {connect} from 'react-redux';
import * as actions from '../../store/actions/index';

class Auth extends Component{
	state= {
		controls:{
			email: {
			elementtype: 'input',
			elementconfig: {
				type: 'email', placeholder: 'enter your email'
			},
		    value: '',
		    validation: {
		    	required: true,
		    	isEmail: true
		    },
		    	valid: false,
		    	touched: false
			},
			password: {
			elementtype: 'input',
			elementconfig: {
				type: 'password', placeholder: 'enter your password'
			},
		    value: '',
		    validation: {
		    	required: true,
		    	minLength: 7
		    },
		    	valid: false,
		    	touched: false
			},
		},
		isSignUp: true
	};
	componentDidMount(){
		if(!this.props.building && this.props.authRedirectPath !== '/')
			this.props.onSetRedirectPath();
	}
	checkValidity(value, rules) {
        let isValid = true;
        if (!rules) {
            return true;
        }
        
        if (rules.required) {
            isValid = value.trim() !== '' && isValid;
        }

        if (rules.minLength) {
            isValid = value.length >= rules.minLength && isValid
        }

        if (rules.maxLength) {
            isValid = value.length <= rules.maxLength && isValid
        }

        if (rules.isEmail) {
            const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            isValid = pattern.test(value) && isValid
        }

        if (rules.isNumeric) {
            const pattern = /^\d+$/;
            isValid = pattern.test(value) && isValid
        }

        return isValid;
    }
    switchAuthModeHandler= () =>{
    	this.setState( prevState => {
    		return{isSignUp: !prevState.isSignUp}    		
    	});
    };
	changeHandler= (event, controlName)=>{
		const updatedControls= {
			...this.state.controls
		};
		updatedControls[controlName]= {
			...this.state.controls[controlName],
			value: event.target.value,
			valid: this.checkValidity(event.target.value, this.state.controls[controlName].validation),
			touched: true
		};
		this.setState({controls: updatedControls});		
	};
	submitHandler= (event) =>{
		event.preventDefault();
		this.props.onAuth(this.state.controls.email.value, this.state.controls.password.value, this.state.isSignUp);
	}
	render() {
		const formElements= [];
		for(let i in this.state.controls){
			formElements.push({
				id: i,
				config: this.state.controls[i]
			})
		};
		let form = formElements.map(el => (
						<Input key= {el.id}
							elementtype= {el.config.elementtype}
							elementconfig= {el.config.elementconfig}
							invalid= {!el.config.valid}
							shouldValidate= {el.config.validation}
							touched= {el.config.touched}
							changed= {(event) =>this.changeHandler(event, el.id)}/>
					));
		let errMessage= null;
		if(this.props.error)
			errMessage= (
				<p>{this.props.error.message}</p>
				);
		if(this.props.loading)
			form= <Spinner/>;
		let authRedirect= null;
		if(this.props.isAuthenticated)
			authRedirect= <Redirect to= {this.props.authRedirectPath} />
		return (
			<div className= {classes.Auth}>
				{authRedirect}
				{errMessage}
				<form onSubmit= {this.submitHandler}>
					{form}
					<button>Submit</button><br/>
					<button onClick= {this.switchAuthModeHandler}>
						Switch to {this.state.isSignUp ? 'Sign in' : 'Sign up'}
					</button>
				</form>
			</div>
		);
	}
};
const mapDispatchToProps= dispatch =>{
	return{
		onAuth: (email,password, isSignUp) => dispatch(actions.auth(email,password, isSignUp)),
		onSetRedirectPath: () => dispatch(actions.setAuthRedirectPath('/'))
	};
};

const mapStateToProps= state =>{
	return{
		loading: state.auth.loading,
		error: state.auth.error,
		isAuthenticated: state.auth.token != null,
		building: state.burger.building,
		authRedirectPath: state.auth.authRedirectPath
	};
};

export default connect(mapStateToProps, mapDispatchToProps)(Auth);