import React from 'react';
import Burger from '../../Burger/Burger';
import classes from './CheckoutSummary.css';

const CheckoutSummary = (props) => {
  return (
    <div className= {classes.CheckoutSummary}>
    	<h1>We hope you like it!</h1>
    	<div style= {{ height: '400px'
    		}}>
    		<Burger  className= {classes.b2} 
    			ingredients= {props.ingredients}/>
    		<button style= {{backgroundColor: 'red', margin: '20px', padding: '20px'}}
    			onClick= {props.cancelled}>Cancel</button>
    		<button style= {{backgroundColor: 'green' , margin: '20px', padding: '20px'}}
    			onClick= {props.proceeded}>Proceed</button>
    	</div>
    </div>
  )
}

export default CheckoutSummary;