import React, {Component} from 'react';
import Auxiliary from '../../hoc/Auxiliary/Auxiliary';
import withErrorHandler from '../../hoc/withErrorHandler/withErrorHandler';
import Burger from '../../components/Burger/Burger';
import BuildControls from '../../components/Burger/BuildControls/buildControls';
import Modal from '../../components/UI/Modal/Modal';
import Spinner from '../../components/UI/Spinner/Spinner';
import OrderSummary from '../../components/Burger/OrderSummary/OrderSummary';
import classes from './BurgerBuilder.css';
import axios from '../../axios-orders';	
import {connect} from 'react-redux';
import * as actions from '../../store/actions/index';

class BurgerBuilder extends Component{
	state= {
		purchasing: false
	};
	componentDidMount(){
		this.props.onInitIngredients();
	}
	updatePurchaseState(ingredients){
		const sum= Object.keys(ingredients)
					.map(igkey =>{
						return ingredients[igkey];
					})
					.reduce((sum, el) =>{
						return sum+el;
					},0);
		return sum > 0;
	}
	
	purchaseHandler= () =>{
		if(this.props.isAuthenticated){
			this.setState({
				purchasing: true
			});
		}
		else{
			this.props.onSetAuthRedirectPath('/checkout');
			this.props.history.push('/auth');
		}
	}
	closeHandler= () =>{
		this.setState({
			purchasing: false
		});
	}
	continueHandler= () =>{
		this.props.onInitBurger();
		this.props.history.push('/checkout');
	}
	
	render(){
		const disabledInfo= {...this.props.ings};
		for(let key in disabledInfo)
			disabledInfo[key] =disabledInfo[key] <=0;
		let order= null;
		//if there is any error
		let burger= (this.props.error ? <p>Ingredients cannot be loaded</p>: <Spinner/>);
		if(this.props.ings){
			burger= (
				<Auxiliary>
					<Burger ingredients= {this.props.ings}/>
					<BuildControls ingredientsAdded= {this.props.onAdd}
						ingredientsDeducted= {this.props.onDelete}
						disabled= {disabledInfo}
						newPrice= {this.props.prc}
						purchasable= {this.updatePurchaseState(this.props.ings)}
						order= {this.purchaseHandler}
						isAuth= {this.props.isAuthenticated}/>
				</Auxiliary>
			);
			order= <OrderSummary ingredients= {this.props.ings}
					cancelOrder= {this.closeHandler}
					continueOrder= {this.continueHandler}
					price= {this.props.prc.toFixed(2)}/>;
		}
		return(
			<Auxiliary>
			
				<Modal show= {this.state.purchasing}
				modalClosed= {this.closeHandler}>
					{order}
				</Modal>
				<div className= {classes.content}>
					{burger}
				</div>
				
			</Auxiliary>
		);
	}
}
const mapStateToProps=state =>{
	return{
		ings: state.burger.ingredients,
		prc: state.burger.price,
		error: state.burger.error,
		isAuthenticated: state.auth.token != null
	}
};
const mapDispatchToProps = dispatch =>{
	return{
		onAdd: (iname) => dispatch(actions.addIngredient(iname)),
		onDelete: (iname) => dispatch(actions.deleteIngredient(iname)),
		onInitIngredients: () => dispatch(actions.initIngredients()),
		onInitBurger: () => dispatch(actions.purchaseInit()),
		onSetAuthRedirectPath: (path) => dispatch(actions.setAuthRedirectPath(path))
	}
};
export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(BurgerBuilder, axios));