import React from 'react';
import {Link} from 'react-router-dom';
import classes from './NavigationItems.css';

const NavigationItems = (props) => {
  return (
    <ul className= {classes.NavigationItems}>
    	<li><Link className= {classes.links}
    		to= '/'>Burger Builder</Link></li>
    	{props.isAuthenticated ? <Link className= {classes.links}
    	 to= '/checkout'>Checkout</Link>: null}
    	 {props.isAuthenticated ? <li><Link className= {classes.links}
    	 to= '/orders'>Orders</Link></li> : null} 
       {!props.isAuthenticated ? <Link className= {classes.links}
         to= '/auth'>Authenticated</Link> : <Link className= {classes.links}
         to= '/logout'>Logout</Link>
         }
    </ul>
  )
}

export default NavigationItems;