import React, {Component} from 'react';
import Auxiliary from '../../../hoc/Auxiliary/Auxiliary';
import Button from '../../UI/Button/Button';

class OrderSummary extends Component{
  render(){
  	const ingredientSummary= Object.keys(this.props.ingredients)
  	.map(igKey => {
  		return (<li key= {igKey}>
  				<span style= {{textTransform: 'capatilize'}}>{igKey}</span>:
  				 {this.props.ingredients[igKey]}
  				 </li>)
  	}); 
  	return (
    <Auxiliary>
    	<h3> Your Order:</h3>
    	<p>Deliciuos burger with the following ingredients:</p>
    	<ul>
    		{ingredientSummary}
    	</ul>
    	<p>Total Price: {this.props.price}</p>
    	<p> Want to Checkout??</p>
    	<Button btnType= "Danger" clicked= {this.props.cancelOrder}>CANCEL</Button>
    	<Button btnType= "Success" clicked= {this.props.continueOrder}>Continue</Button>
    </Auxiliary>
  )
  }
}

export default OrderSummary;