import React, {Component} from 'react';
import {connect} from 'react-redux';
import classes from './ContactData.css';
import axios from '../../../axios-orders';
import Input from '../../../components/UI/Input/Input';
import withErrorHandler from '../../../hoc/withErrorHandler/withErrorHandler';
import * as actions from '../../../store/actions/index';
import Spinner from '../../../components/UI/Spinner/Spinner';

class ContactData extends Component{
state= {
	orderForm: {
		name: {
			elementtype: 'input',
			elementconfig: {
				type: 'text', placeholder: 'enter your name'
			},
		    value: '',
		    validation: {
		    	required: true
		    },
		    valid: false,
		    touched: false
		},
		mobileNo:{
			elementtype: 'input',
			elementconfig: {
				type: 'text', placeholder: 'enter your mobile number'
			},
		    value: '',
		    validation: {
		    	required: true,
		    	minlength: '1',
		    	maxlength: '10',
		    	isNumeric: true
		    },
		    	valid: false,
		    	touched: false
		},
		email: {
			elementtype: 'email',
			elementconfig: {
				type: 'text', placeholder: 'enter your email id'
			},
		    value: '',
		    validation: {
		    	required: true,
		    	isEmail: true
		    },
		    	valid: false,
		    	touched: false
		},
		deliveryMode: {
			elementtype: 'select',
			elementconfig: {
				options: [
				{
					value: 'takeout', displayValue: 'Takeout'
				},
				{
					value: 'selfservice', displayValue: 'SelfService'
				}]
			},
			value: 'selfservice',
			valid: true
		}
	},
	loading: false,
	formValid: false
};

orderHandler= (event)=>{	//it handles sending data to server
	event.preventDefault();
	const formData= {};
	for(let el in this.state.orderForm){
		formData[el]= this.state.orderForm[el].value;
	}
	// console.log(this.props.ingredients);
		this.setState({
				loading: true
			});
		const order= {	//data that is sent to server
			ingredients: this.props.ings,
			price: this.props.prc,
			orderData: formData,
			userId: this.props.userId
		}
		this.props.onPurchaseBurger(order, this.props.token);
}
checkValidity(value, rules) {
        let isValid = true;
        if (!rules) {
            return true;
        }
        
        if (rules.required) {
            isValid = value.trim() !== '' && isValid;
        }

        if (rules.minlength) {
            isValid = value.length >= rules.minlength && isValid
        }

        if (rules.maxlength) {
            isValid = value.length <= rules.maxlength && isValid
        }

        if (rules.isEmail) {
            const pattern = /[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?/;
            isValid = pattern.test(value) && isValid
        }

        if (rules.isNumeric) {
            const pattern = /^\d+$/;
            isValid = pattern.test(value) && isValid
        }

        return isValid;
    }

changeHandler= (event, id)=>{
		const updatedForm= {
			...this.state.orderForm
		};
		const updatedFormElement= {...updatedForm[id]};
		updatedFormElement.value= event.target.value;
		updatedForm[id]= updatedFormElement;
		updatedFormElement.valid=  this.checkValidity(updatedFormElement.value, updatedFormElement.validation);
		updatedFormElement.touched= true;
		let formValid= true;
		for(let el in updatedForm){
			formValid= updatedForm[el].valid && formValid;
		}
		this.setState({
			orderForm: updatedForm,
			formValid: formValid
		});

		console.log(updatedFormElement);
	}
render() {
	const formElements= []; //convert object into array
	for(let key in this.state.orderForm){
		formElements.push({
			id: key,
			attri: this.state.orderForm[key]
		})
	};
	let form =(
		<form onSubmit= {this.orderHandler}>
				{
					formElements.map(formElement => (
						<Input key= {formElement.id} 
							elementtype={formElement.attri.elementtype}
							elementconfig= {formElement.attri.elementconfig}
							value= {formElement.attri.value}
							invalid= {!formElement.attri.valid}
							shouldValidate= {formElement.attri.validation}
							touched= {formElement.attri.touched}
							changed= {(event) =>this.changeHandler(event, formElement.id)}/>
				))}
				<button disabled= {!this.state.formValid} 
					onClick= {this.orderHandler}>Order now!</button>
			</form>
	);
	if(this.props.loading){
		form= <Spinner/>;
	}
	return (
		<div className= {classes.ContactData}>
			<h3>
				Enter your information
			</h3>
			{form}
		</div>
	);
}
}
const mapStateToProps= state =>{
	return{
		ings: state.burger.ingredients, 
		prc: state.burger.price,
		loading: state.order.loading,
		token: state.auth.token,
		userId: state.auth.userId
	}
};
const mapDispatchToProps= dispatch =>{
	return{
		onPurchaseBurger: (orderData, token) => dispatch(actions.purchaseBurger(orderData, token))
	}
}
export default connect(mapStateToProps, mapDispatchToProps)(withErrorHandler(ContactData,axios));